<template>
  <div class="circular-menu">
    <ul class="nav">
      <li
        v-for="(item, index) in nav"
        :key="`navlink-${index}`"
      >
        <a
          :href="item.link ? item.link : 'javascript:void(0)'"
          :class="{ active: item.active }"
          class="nav-link"
          @click="linkClicked(item)"
        >
          <icon
            :is-active="item.active"
            :icon-name="item.icon"
          />
          {{ item.title }}
        </a>
      </li>
    </ul>

    <menu-path :active-index="activeItemIndex" />
  </div>
</template>

<script>
import Icon from './icons/index.vue'
import MenuPath from './icons/path.vue'

export default {
  components: {
    Icon,
    MenuPath,
  },
  data() {
    return {
      nav: [
        {
          title: 'Mobility',
          icon: 'mobility',
          link: '',
          active: false,
        },
        {
          title: 'IoT BMS',
          icon: 'iot',
          link: '',
          active: false,
        },
        {
          title: 'Sustainability',
          icon: 'sustainability',
          link: '',
          active: true,
        },
        {
          title: 'Retrofitting',
          icon: 'retrofitting',
          link: '',
          active: false,
        },
        {
          title: 'Recycling',
          icon: 'recycling',
          link: '',
          active: false,
        },
      ],
    }
  },
  computed: {
    activeItemIndex() {
      return this.nav.findIndex(e => e.active)
    },
  },
  methods: {
    linkClicked(item) {
      this.nav.forEach(e => {
        e.active = item === e
      })
    },
  },
}
</script>

<style lang="scss" scoped>
@import '@/assets/scss/variables/_variables';

.circular-menu {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 9;
  overflow: hidden;
  padding-top: 100px;
  text-align: center;
  pointer-events: none;
}
ul.nav {
  width: 100%;
  max-width: 700px;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: relative;
  pointer-events: auto;
}
ul.nav:before {
  content: "";
  position: absolute;
  top: 0;
  left: -20%;
  right: -20%;
  height: 300px;
  border-radius: 100%;
  background: #4B4B4B;
  filter: blur(30px);
  z-index: -1;
}
ul.nav > li {
  margin: 0 10px;
  width: 100px;
}
ul.nav > li:nth-child(odd){
  transform: translateY(20px);
}
ul.nav > li:nth-child(even){
  transform: translateY(5px);
}
ul.nav > li:nth-child(3n){
  transform: translateY(0);
}
ul.nav .nav-link {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: $light;
  padding-left: 0;
  padding-right: 0;
}
ul.nav .nav-link.active {
  color: #fff;
  font-weight: bold;
}
</style>
